(function() {
    'use strict';

    angular
        .module('app', ['ui.router']);

})();

(function () {

    angular.module('app')
        .controller('NewsController', NewsController);

    function NewsController( $scope, $rootScope, $state, $stateParams) {
        alert("Hola NewsController");
    }

})();

(function () {

    angular.module('app')
        .controller('AnalyticsController', AnalyticsController);

    function AnalyticsController( $scope, $rootScope, $state, $stateParams) {
        alert("Hola AnalyticsController");
    }

})();

(function() {
    'use strict';

    angular
        .module('app')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $state,$http){

    }

})();
(function() {
    'use strict';

    angular
        .module('app')
        .config(routerConfig);

    function routerConfig($stateProvider, $urlRouterProvider) {
        $stateProvider
            .state('news', {
                url: '/news',
                data: {
                    displayName: 'News'
                },
                views: {
                    "@": {
                        templateUrl: 'app/components/news/news.html',
                        controller: 'NewsController',
                        controllerAs: 'news'
                    }
                }
            })
            .state('analytics', {
                url: '/analytics',
                data: {
                    displayName: 'Analytics'
                },
                views: {
                    "@": {
                        templateUrl: 'app/components/analytics/analytics.html',
                        controller: 'AnalyticsController',
                        controllerAs: 'analytics'
                    }
                }
            })
        ;

        $urlRouterProvider.otherwise('/news');
    }

})();

(function() {
    'use strict';

    angular.module('app')
        .config(config);

    function config($logProvider) {
        $logProvider.debugEnabled(true);
    }

})();

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/components/analytics/analytics.html","<div><div class=\"barra-lateral\"><ul><li>Navigates</li><li>Analytics</li><li>Navigates</li><li>Navigates</li><li>Navigates</li></ul></div></div>");
$templateCache.put("app/components/news/news.html","<div><div class=\"top\"><img src=\"assets/img/r-3-l-logo-application-02.png\" srcset=\"assets/img/r-3-l-logo-application-02@2x.png 2x, assets/img/r-3-l-logo-application-02@3x.png 3x\" class=\"R3L_Logo_Application_02\"></div></div>");}]);